import React, { useEffect, useState } from 'react';
import { useAppDispatch, useAppSelector } from '../../../hook/redux';
import { UserAddress, UserPhone, GetState } from '../../../types';
import { updateUser } from '../../../redux/slices/userSlice';


interface Props {
    close: () => void;
}

const EditProfile: React.FC<Props> = ({ close }) => {

    const dispatch = useAppDispatch();
    const user = useAppSelector(state => state.userReducer);

    const [firstName, setFirstName] = useState(user.firstName);
    const [lastName, setLastName] = useState(user.lastName);
    const [numberPhone, setNumberPhone] = useState<UserPhone>({
        countryCode: user?.numberPhone?.countryCode || 0,  //    
        phoneNumber: user?.numberPhone?.phoneNumber || '',
    });
    const [address, setAddress] = useState<UserAddress>(user.address);
    console.log('setFirstName', 'setLastName', 'setPhone', 'setAddress', setFirstName, setLastName, setNumberPhone, setAddress);

    const phone = user.phone;
    const updateNumberPhoneFromPhone = (phone: string) => {
        const parts = phone.split(')');
        //    
        if (parts.length === 2) {
            //         
            const countryCode = parseInt(parts[0].replace('(', ''), 10);
            const phoneNumber = parts[1].trim(); // //   
            setNumberPhone({
                countryCode: isNaN(countryCode) ? 0 : countryCode,
                phoneNumber,
            })
            console.log('countryCode', countryCode);  // 955
            console.log('phoneNumber', phoneNumber);  // 521111111
        } else {
            console.log("   ");
        }
    }

    //    phone
    useEffect(() => {
        updateNumberPhoneFromPhone(phone);
    }, [phone]);

    const handleClickSave = () => {
        const userProfile = {
            firstName,
            lastName,
            numberPhone,
            address,
            meta: {
                requireToken: true,
            }
        };
        console.log('userProfile:', userProfile);
        dispatch(updateUser(userProfile));

        close();
       
    };

    const handleClickReset = () => {
        setFirstName('');
        setLastName('');
        setNumberPhone({
            countryCode: 0,
            phoneNumber: '',

        });
        setAddress({
            country: '',
            city: '',
            zipCode: 0,
            street: '',
            house: '',
            fullAddress: ''
        });
    };

    return (
        <div>
            <label> First name:
                <input
                    type='text'
                    value={firstName}
                    onChange={(e) => setFirstName(e.target.value.trim())}
                />
            </label>
            <label> Last name:
                <input
                    type='text'
                    value={lastName}
                    onChange={(e) => setLastName(e.target.value.trim())}
                />
            </label>
            <label>
                Phone Country Code:
                <input
                    type='text'
                    value={numberPhone.countryCode}
                    onChange={(e) =>
                        setNumberPhone({
                            ...numberPhone,
                            countryCode: parseInt(e.target.value),
                        })

                    }
                />
            </label>
            <label>
                Phone Number:
                <input
                    type='phone'
                    value={numberPhone.phoneNumber}
                    // value={phoneNumber}
                    onChange={(e) =>
                        setNumberPhone({
                            ...numberPhone,
                            phoneNumber: e.target.value,
                        })
                    }
                />
            </label>
            <label>
                Country:
                <input
                    type='text'
                    value={address.country}
                    onChange={(e) =>
                        setAddress({
                            ...address,
                            country: e.target.value,
                        })
                    }
                />
            </label>
            <label>
                City:
                <input
                    type='text'
                    value={address.city}
                    onChange={(e) =>
                        setAddress({
                            ...address,
                            city: e.target.value,
                        })
                    }
                />
            </label>
            <label>
                Zip Code:
                <input
                    type='text'
                    value={address.zipCode}
                    onChange={(e) =>
                        setAddress({
                            ...address,
                            zipCode: parseInt(e.target.value),
                        })
                    }
                />
            </label>
            <label>
                Street:
                <input
                    type='text'
                    value={address.street}
                    onChange={(e) =>
                        setAddress({
                            ...address,
                            street: e.target.value,
                        })
                    }
                />
            </label>
            <label>
                House:
                <input
                    type='text'
                    value={address.house}
                    onChange={(e) =>
                        setAddress({
                            ...address,
                            house: e.target.value,
                        })
                    }
                />
            </label>
            <button onClick={handleClickSave}>Save</button>
            <button onClick={handleClickReset}>Reset</button>
            <button onClick={close}>Close</button>
        </div>
    )
};

export default EditProfile;

